@import "../components/variables";

@media screen and (max-width: 1220px), screen and (max-device-width: 1220px){
  body{
    .catalog{
      .container{
        .content__catalog{
          .catalog__options{
            &__goods,
            &__sortBy{
              select{
                width: 160px;
              }
            }
          }
          .catalog__goods{
            div{
              width: 200px;
              a{
                img{
                  width: 175px;
                  height: 175px;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1087px), screen and (max-device-width: 1087px){
  body{
    .catalog{
      .container{
        .content__catalog{
          .catalog__goods{
            div{
              width: 180px;
              a{
                img{
                  width: 175px;
                  height: 175px;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1024px), screen and (max-device-width: 1024px){
  body{
    .catalog{
      .container{
          grid-template-columns: 30% 70%;
        .content__catalog{
          .catalog__options{
            &__goods,
            &__sortBy{
              display: none;
            }
            .mobile{
              display: flex;
            }
          }
          .catalog__goods{
            div{
              width: 150px;
              a{
                img{
                  width: 135px;
                  height: 135px;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 920px), screen and (max-device-width: 920px){
  body{
    .catalog{
      .container{
        grid-template-areas: "breadcrumb breadcrumb"
                             "catalog catalog"
                             "aside aside"
                             "order order";
        .content__catalog{
          .catalog__goods{
            justify-content: space-around;
            div{
              margin: 15px 0;
              width: 30%;
              a{
                img{
                  width: 175px;
                  height: 175px;
                }
              }
            }
          }
          .catalog__showMore,
          .catalog__paginator,
          .catalog__aboutCategory{
            width: 100%;
          }
        }
        .content__aside{
          width: 100%;
          div{
            ul{
              height: 200px;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 720px), screen and (max-device-width: 720px){
  body{
    .catalog{
      .container{
        .content__catalog{
          .catalog__goods{
            div{
              width: 45%;
            }
          }
          .catalog__paginator{
            .desktop{
              display: none;
            }
            .mobile{
              display: flex;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 480px), screen and (max-device-width: 480px) {
  body{
    .catalog{
      .container{
        .content__catalog{
          .catalog__goods{
            div{
              width: 225px;
              a{
                img{
                  width: 200px;
                  height: 200px;
                }
              }
            }
          }
        }
      }
    }
    .catalog__options__mobile{
      width: 290px;
    }
  }
}